import { AdditionalRegistrationModel } from '@@addons/interfaces/additional_registration';
import { LitElement, html, css } from 'lit';
import { customElement, property, query, queryAll } from 'lit/decorators.js'
import '@material/mwc-select';
import '@material/mwc-textarea';


@customElement("question-inputs-component")
export class QuestionInputsComponent extends LitElement {
  constructor() { super(); }

  @property({ type: String })
  private inputId = Math.random().toString(36).substring(2, 22);

  @property({ type: String })
  public questionValue?: string;

  @property({ type: String })
  public placeholderInstructionValue?: string;

  @property({ type: String })
  public typeValue?: string;

  @property({ type: String })
  public branchIdValue?: string;

  @property({ type: String })
  public memberCategoryIdValue?: string;

  @property({ type: Array })
  public meetingQuestion: Array<AdditionalRegistrationModel> = [];

  private defaultQuestions?: AdditionalRegistrationModel = null;

  connectedCallback() {
    super.connectedCallback();
    if (this.meetingQuestion == undefined) this.meetingQuestion = [];

    this.defaultQuestions = this.meetingQuestion.length < 0 ? null: this.meetingQuestion[0] == undefined? null: this.meetingQuestion[0];

    if(this.defaultQuestions !== null) {
      this.typeValue = String(this.defaultQuestions.type);
      this.questionValue = this.defaultQuestions.question;
      this.branchIdValue = String(this.defaultQuestions.branchID.id);
      this.memberCategoryIdValue = String(this.defaultQuestions.memberCategoryID.id);
      this.placeholderInstructionValue = this.defaultQuestions.placeholderInstruction;

    }
  }

  disconnectedCallback() { }
  static styles = [
    css`
   :host { display: block; }
  `
  ];

  render() {

    return html`
      <div class="container my-4">
        <div class="row justify-center">
          <div class="col-md-6 col-lg-6">
            <h4 class="font-semibold my-2">Select Question Type</h4> 
            <mwc-textfield name="type" type="question" class="w-full" id="startQuestion" @change="${this.handleTypeValueChange}" value="${String(this.defaultQuestions == null? "": this.defaultQuestions.type)}" label="Select Start Question" outlined required>
            </mwc-textfield>
            <mwc-select name="type" class="w-full" id="type" label="Select Question Type" outlined required @change="${this.handleTypeValueChange}">
              <mwc-list-item value="0">Select Question Type</mwc-list-item>
              <mwc-list-item value="1">Select Question Type</mwc-list-item>
              <mwc-list-item value="2">Select Question Type</mwc-list-item>
            </mwc-select>
          </div>
          <div class="col-md-6 col-lg-6">
            <h4 class="font-semibold my-2">Select Close Question</h4> 
            <mwc-textfield name="closeQuestion" type="question" class="w-full" id="endQuestion" @change="${this.handleQuestionValueChange}" value="${String(this.defaultQuestions == null? "": this.defaultQuestions.question)}" label="Select End Question" outlined required>
            </mwc-textfield>
          </div>
          <div class="col-md-6 col-lg-6">
            <h4 class="font-semibold my-2">Select Start Question</h4> 
            <mwc-textfield name="startQuestion" type="question" class="w-full" id="startQuestion" @change="${this.handleBranchIdValueChange}" value="${String(this.defaultQuestions == null? "": this.defaultQuestions.branchID.id)}" label="Select Start Question" outlined required>
            </mwc-textfield>
          </div>
          <div class="col-md-6 col-lg-6">
            <h4 class="font-semibold my-2">Select Close Question</h4> 
            <mwc-textfield name="closeQuestion" type="question" class="w-full" id="endQuestion" @change="${this.handleMemberCategoryIdValueChange}" value="${String(this.defaultQuestions == null? "": this.defaultQuestions.memberCategoryID.id)}" label="Select End Question" outlined required>
            </mwc-textfield>
          </div>
          <div class="col-md-6 col-lg-6">
            <h4 class="font-semibold my-2">Select Close Question</h4> 
            <mwc-textfield name="closeQuestion" type="question" class="w-full" id="endQuestion" @change="${this.handlePlaceholderInstructionValueChange}" value="${String(this.defaultQuestions == null? "": this.defaultQuestions.placeholderInstruction)}" label="Select End Question" outlined required>
            </mwc-textfield>
          </div>
        </div>
      </div>
    `;
  }

  handleTypeValueChange(event: Event) {
    // @ts-ignore
    this.startQuestionValue = event.target.value;
  }

  handleQuestionValueChange(event: Event) {
    // @ts-ignore
    this.endQuestionValue = event.target.value;
  }

  handleBranchIdValueChange(event: Event) {
    // @ts-ignore
    this.startQuestionValue = event.target.value;
  }

  handleMemberCategoryIdValueChange(event: Event) {
    // @ts-ignore
    this.endQuestionValue = event.target.value;
  }

  handlePlaceholderInstructionValueChange(event: Event) {
    // @ts-ignore
    this.endQuestionValue = event.target.value;
  }

  firstUpdated() { 
  }

  createRenderRoot() {
    return this;
  }
}
